<template>
  <div class="register">
    <!-- 登录 -->
    <div class="landing-text">
      <!-- 登录 -->
      <div class="regegit">
        <van-cell-group :maxlength="12">
          <van-field
            v-model="user.value"
            :error-message="user.errorMessage"
            left-icon="contact"
            class="animated zoomInDown"
            placeholder="账号"
            @input="validUP(user.value, 'validUsername', {key: 'user', errorMessage: '账号支持4-8个(字母数字_)组合，必须字母开头'})"
          />
          <van-field
            v-model="pwd1.value"
            :type="pwd1.type"
            left-icon="bag-o"
            class="animated zoomInDown"
            placeholder="密码"
            :right-icon="pwd1.icon"
            :error-message="pwd1.errorMessage"
            @click-right-icon="checkPassword('pwd1')"
            @input="validUP(pwd1.value, 'validPwd', {key: 'pwd1', errorMessage: '密码支持6-16个(字母数字_)组合，必须含有大或小字母'})"
          />
        </van-cell-group>
        <div class="btn">
          <van-button
            icon="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1627475845,3049836284&fm=26&gp=0.jpg"
            round
            type="info"
            size="20"
            class="animated zoomInUp"
            @click="bthding()"
            :loading="isLoading"
            :disabled="!isValid"
            color="linear-gradient(to right, red, #B02125)"
            loading-text="登录中..."
          >点击登录</van-button>
        </div>
      </div>
    </div>
    <div class="chuce animated bounceInDown">
      没有账号,建议去注册
      <strong>|</strong>
      <span class="animated bounceInDown" @click="push({name:'login'})">注册</span>
    </div>
  </div>
</template>

<script>
import { validForm } from "../../tool/validForm";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("loginModule");
export default {
  data() {
    return {};
  },

  computed: {
    // 传递的是属性
    ...mapState(["isLoading", "user", "pwd1", "isValid"])
  },
  methods: {
    //传递的是方法
    ...mapMutations(["checkPassword"]),
    //登录
    bthding() {
      this.reloading = true;
      this.$store.commit("loginModule/login", true);
      let randomTime = Math.random() * 5 * 1000;
      setTimeout(() => {
        //获取所有用户
        let users = localStorage.getItem("users");
        users = users ? JSON.parse(users) : [];
        if (users.length === 0) {
          this.$toast({
            duration: 2000,
            message: "该用户不存在1"
          });
          this.$store.commit("loginModule/login", false);
          return;
        }
        //查找用户
        
        for (let i = 0; i < users.length; i++) {
          if (users[i].username == this.user.value) {
            //如果该用户存在
            //匹配密码
            if (users[i].pwd == this.pwd1.value) {
              this.$toast({
                duration: 2000,
                message: "登录成功"
              });
              this.$store.commit("loginModule/login", false);
              //跳转到首页
              //记录用户登录状态
              let user = {
                isLogin: true,
                sex:users[i].sex,
                username: this.user.value
              };
              localStorage.setItem("user", JSON.stringify(user));
             
              if(users[i].sex == 'kkk'){
                 return this.$router.push({ name: "home" });
              }
              return this.$router.push({ name: "home" });
            } else {
              this.$toast({
                duration: 2000,
                message: "用户名或者密码不正确"
              });
              this.$store.commit("loginModule/login", false);
              return;
            }
          }
        }
        //用户不存在
        this.$toast({
          duration: 2000,
          message: "该用户不存在"
        });
        this.$store.commit("loginModule/login", false);
      }, randomTime);
    },
    //注册界面
    push(o) {
      this.$router.push(o);
    },
    //验证用户名或者密码
    validUP(value, fnName, o) {
      let isValid = validForm[fnName](value);

      o.isValid = isValid;

      this.$store.commit("registerModule/validUP", o);

      this.$store.commit("registerModule/passForm");
    }
  },
  components: {},
  created() {
    //清空账号密码
    this.user.value = '';
    this.pwd1.value = '';

  }
};
</script>
<style lang="less">
.van-hairline--top-bottom::after, .van-hairline-unset--top-bottom::after{
  border-width: 0;
}
.register {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: url("../../image/背景.png") no-repeat center center;
  background-size: 100% 100%;
  .landing-text {
    padding: 0 50px;
    margin-top: 50%;
    .regegit {
      overflow: hidden;
      position: relative;
      .van-cell-group {
        background-color: transparent;

        .van-field__left-icon {
          margin-right: 10px;
        }
        .van-cell {
          background-color: rgba(0, 0, 0, 0.2);
          margin-bottom: 10px;
          border-radius: 20px;
        }
      }
      .btn {
        margin-top: 15px;
        button {
          padding: 0 80px;
          display: block;
          margin: 0 auto;
          box-shadow: 0px 1px 2px 0px black;
        }
      }
    }
  }
  .chuce {
    width: 100%;
    line-height: 30px;
    font-size: 12px;
    text-align: center;
    position: absolute;
    color: white;
    left: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.2);
    strong {
      margin: 0 5px;
    }
    span {
      color: black;
    }
  }
}
</style>